body {
    color: #5e6472;
    font-family: "Nunito", monospac, serif;
    line-height: 1.7;
    font-size: 16px;
    font-weight: 400;
    margin: 0;
    padding: 0;
}
.example {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    width: 250px;
    margin: 15px 10px;
    background-color: white;
    padding: 20px;
    box-shadow: 0 25px 50px -12px rgb(0 0 0);
    position: relative;
}

.exampleBox {
    background: #fff;
    padding: 20px;
    box-shadow: 0 25px 50px -12px rgb(0 0 25%);
    margin: 15px 0;
    position: relative;
}

.exampleBox .exampleImg {
    position: relative;
    padding-bottom: 20px;
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: start;
    color: #5e6472;
    font-family: 'Nunito Sans', sans-serif;
    line-height: 1.7;
    font-size: 16px;
    font-weight: 400;
}

h5 {
    font-size: 1.25rem;
    line-height: 1.2;
    margin: 0 0;
    font-weight: 600;
    color: #1c1d24;
}
.workLink {
    display: block;
    position: relative;
    overflow: hidden;
    cursor: zoom-in!important;
}
.workLink:after {
    content: "";
    position: absolute;
    bottom: 0;
    top: auto;
    left: 0;
    right: 0;
    height: 0;
    width: 100%;
    transition: all .55s ease;
    background: rgba(220,150,90,.8);
    pointer-events: none;
}

a {
    color: #dc965a;
    transition: .3s;
    text-decoration: none;
    background-color: transparent;
}

.galleryLink {
    display: block;
    position: relative;
    overflow: hidden;
    cursor: zoom-in!important;
}
.galleryLink:hover:after {
    content: "";
    position: absolute;
    bottom: 0;
    top: auto;
    left: 0;
    right: 0;
    height: 0;
    width: 100%;
    transition: all .55s ease;
    background: rgba(220,150,90,.8);
    pointer-events: none;
}
.image {
    transition: .6s;
    margin: auto;
    display: block;
    max-width: 100%;
    vertical-align: middle;
    border-style: none;
    cursor: zoom-in!important;
}
